@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
.search {
    flex-basis: 95%;
    flex-shrink: 1;
    color: rgba(65, 69, 73, 0.7);
}

.search fieldset {
    margin: 0;
    padding: 0;
    border: none;
}

.search__label {
    display: flex;
    position: relative
}

.search__icon {
    position: absolute;
    top: 1rem;
    left: 0.25rem;
    font-size: 1rem !important;
}

.search input[type=search] {
    width: 100%;
    border: none;
    font-family: inherit;
    padding: 1rem 0.25rem 1rem 1.45rem;
    background-color: #f1f3f4;
    color: rgba(65, 69, 73, 0.7);
    border-radius: 3px;
    -webkit-transition: background 100ms ease-in, width 100ms ease-out;
    transition: background 100ms ease-in, width 100ms ease-out;
}

.search input[type=search]:focus {
    outline: none;
    background-color: rgb(255, 255, 255, 1);
    color: rgba(65, 69, 73, 0.6);
    box-shadow: 0 1px 1px 0 rgba(65, 69, 73, 0.3), 0 1px 3px 1px rgba(65, 69, 73, 0.15);
}
.header {
    position: fixed;
    padding: 10px;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: rgba(255, 255, 255, 1);
    box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.2);
}

.header aside {
    padding-right: 15px;
}

.header__title {
    font-size: 1.3rem;
    flex-shrink: 0;
    line-height: 0px;
    padding-top: 10px
}

.header time {
    font-size: 0.8rem;
}
.toolbar {
    grid-area: toolbar;
    display: flex;
    justify-content: center;
    font-size: 1rem;
    background: rgba(255,229,100,0.2);
    border-radius: 15px;
    padding: 10px;
    margin-top: 1.5rem;
    align-items: center;
    margin-bottom: -1rem;
    border: 1px solid rgba(0,0,0,0.2);
}

.toolbar__item {
    display: flex;
    align-items: center;
    padding-right: 20px;
}

.toolbar__item__checkbox {
    display: flex;
    align-items: center;
    padding-right: 7px;
}

.toolbar__item label {
    padding-right: 10px;
}

.toolbar__item select {
    font-size: 1rem;
    font-family: 'Montserrat', sans-serif;
}
.article {
    color: rgba(0, 0, 0, .68);
    padding-top: 1rem;
    display: grid;
    grid-template-areas: "title" "description" "figure" "content";
    grid-template-rows: -webkit-min-content -webkit-min-content -webkit-min-content -webkit-min-content;
    grid-template-rows: min-content min-content min-content min-content;
    grid-template-columns: 1fr;
    grid-gap: 1rem;
    text-align: justify;
}

.article__title {
    grid-area: title;
    font-size: 1.3rem;
}

.article__title a {
    color: rgba(0, 0, 0, .86);
}

.article__figure {
    grid-area: figure;
}

.article__figure img {
    object-fit: cover;
    width: 100%;
    border-radius: 8px;
}

.article__description {
    grid-area: description;
    font-size: 14px;
}

.article__description mark {
    display: flex;
    justify-content: center;
    background: none;
    color: inherit;
    padding-top: 0.5rem;
}

.article__description time {
    color: rgba(0, 0, 0, 0.45);
}

.article__author {
    padding-right: 10px;
}

.article__content {
    grid-area: content;
    font-size: 16px;
}

@media screen and  (min-width: 767px) {
    .article {
        grid-template-areas: "title title figure" "description description figure" "content content content";
        grid-template-rows: -webkit-min-content 1fr -webkit-min-content;
        grid-template-rows: min-content 1fr min-content;
        grid-template-columns: 1fr 1fr -webkit-min-content;
        grid-template-columns: 1fr 1fr min-content;
    }

    .article__title {
        font-size: 1.8rem;
        text-align: end;
    }

    .article__description{
        text-align: end;
    }

    .article__description mark {
        justify-content: flex-end;
    }

    .article__figure {
        place-self: center end;
    }

    .article__figure img {
        height: 10rem;
        width: 10rem;
    }
}
.pagination__input {
    margin: 0 0.5rem;
    width: 2rem;
    font-size: 1rem;
    padding: 0.2rem;
    font-family: 'Montserrat', sans-serif;
    border: 1px solid #bcbdbd;
    border-radius: 8px;
}

.pagination__input:focus, .pagination__buttons button {
    outline: none;
}

.pagination__buttons {
    margin-left: 1rem;
}

.pagination__buttons button {
    background: rgba(0, 0, 0, .05);
    border: none;
    box-shadow: 0 1px 3px 0 #bcbdbd, 0 0 0 1px #d4d4d5;
    font-size: 1rem;
    padding: 0.3rem 0.5rem;
    font-family: 'Montserrat', sans-serif;
}

.pagination__button-left {
    border-radius: 8px 0 0 8px;
}

.pagination__button-right {
    border-radius: 0px 8px 8px 0px;
}

.pagination-centered {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 1rem;
}

.lds-ripple {
    display: block;
    position: relative;
    width: 80px;
    height: 80px;
    margin: 25px auto 0 auto;
}
.lds-ripple div {
    position: absolute;
    border: 4px solid rgba(0, 0, 0, .68);
    opacity: 1;
    border-radius: 50%;
    -webkit-animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
            animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
    -webkit-animation-delay: -0.5s;
            animation-delay: -0.5s;
}
@-webkit-keyframes lds-ripple {
    0% {
        top: 36px;
        left: 36px;
        width: 0;
        height: 0;
        opacity: 1;
    }
    100% {
        top: 0px;
        left: 0px;
        width: 72px;
        height: 72px;
        opacity: 0;
    }
}
@keyframes lds-ripple {
    0% {
        top: 36px;
        left: 36px;
        width: 0;
        height: 0;
        opacity: 1;
    }
    100% {
        top: 0px;
        left: 0px;
        width: 72px;
        height: 72px;
        opacity: 0;
    }
}
.source {
    text-align: center;
    padding: 5px;
    margin: 25px 0 10px 0;
    box-shadow: 0 1px 3px 0 #bcbdbd, 0 0 0 1px #d4d4d5;
    border-radius: 8px;
    font-size: 16px;
    cursor: pointer;
}

.source:hover {
    background: red;
    background: rgba(0,0,0,.05);
}

.source__category {
    color: rgba(0, 0, 0, 0.45);
}

.main {
    padding: 70px 1.5vw 0 1.5vw;
    width: inherit;
    display: grid;
    grid-template-areas: "articles" "sourceContainer";
    grid-template-rows: -webkit-min-content -webkit-min-content;
    grid-template-rows: min-content min-content;
    grid-template-columns: 1fr;
    grid-gap: 2rem;
    max-width: 1200px;
    margin: 0 auto;
}

@media screen and  (min-width: 767px) {
    .main {
        grid-template-columns: 1fr 270px;
        grid-template-rows: -webkit-max-content;
        grid-template-rows: max-content;
    }
}

@media screen and (min-width: 1024px) {
    .main {
        grid-template-areas:    "toolbar toolbar"
                                "articles sourceContainer";
        grid-template-columns: 1fr 350px;
        grid-template-rows: -webkit-min-content 1fr;
        grid-template-rows: min-content 1fr;
    }
}
* {
  margin: 0;
  padding: 0;
}

body {
  font-size: 20px;
  font-family: 'Montserrat', sans-serif;
}
